import { Block, Node } from '@ali/4ever-cangjie';
import { JsonML } from '@ali/4ever-utils';
import RefBlock from '../models';

export default {
  name: RefBlock.TYPE,

  match(node: Node): boolean {
    return RefBlock.isRefBlock(node);
  },

  convert(node: Block): JsonML {
    return [RefBlock.TYPE, node.data];
  },
};
